function countTotalPrice() {
  let nTotalCount = 0,
    dopPrice = 0,
    $findParrent = '';


  if ($(window).width() > 760) {
    $findParrent = $('.preview-way__mobile');
  } else {
    $findParrent = $('.preview-way__right');
  }
  $findParrent.find('.preview-way__counting-el').each(function () {
    let nCost = Number($(this).attr('data-cost'));

    //Размер доп услуг
    if ($(this).attr('data-name')) {
      dopPrice += nCost;
    }

    nTotalCount += nCost;
  });

  // Для агента сумма всех доп услуг/0.97
  if ($('.additional-select input[name=customdoctype]').val() == 'agent') {
    nTotalCount = nTotalCount - dopPrice + dopPrice / Number($('.additional-select__el[data-agent]').data('price'));
  }

  let $agent = $('[data-agent]');
  let sumPriceRaw = $("input[name='sum_price_raw']").val();
  let deliveryPriceRaw = $("input[name='delivery_price_raw']").val();
  let agentPrice = (sumPriceRaw - deliveryPriceRaw) * $agent.attr('data-price');

  $(".preview-way__counting-el[data-name='customdoctype']").attr("data-cost",agentPrice);
  $(".preview-way__counting-el[data-name='customdoctype']").find("div:nth-of-type(2) span").html('<sub>$</sub> ' + number_format(agentPrice, 0, '.', ' '));
  $("input[name='customdoctype']").val("Агентский Контракт/"+agentPrice);

  $('.preview-way__all span:nth-of-type(2)').html('<sub>$</sub> ' + number_format(nTotalCount, 0, '.', ' '));
  $('input[name="sum_price"]').val(number_format(nTotalCount, 0, '.', ' '));
  $('input[name="sum_price_raw"]').val(number_format(nTotalCount, 0, '.', ''));
}

function customModalInput() {
  // изменение ширины и наложение маски на инпут в дополнительных услугах карточки маршрута
  $('.additional-input').each(function () {
    let $this = $(this),
      $input = $this.find('input'),
      $rightCounting = $('.preview-way__counting'),
      tInputName = $input.attr('name'),
      tInputVal = $input.val();



    $input.inputmask({
      'alias': 'decimal',
      rightAlign: true,
      'groupSeparator': ' ',
      'autoGroup': true,
      'min': 0,
      'max': 10000000
    });

    $this.append('<div class="buffer">' + $input.val() + '</div>');

    let $buffer = $this.find('.buffer'),
      nBufferWidth = Number($buffer.width());

    setTimeout(function () {
      $input.css({
        'width': nBufferWidth + 2 + 'px',
        'flex': '0 0 ' + (nBufferWidth + 2) + 'px'
      });
    }, 1);

    $input.on('input', function () {
      $buffer.text($input.val());
      nBufferWidth = Number($buffer.width());
      $input.css({
        'width': nBufferWidth + 2 + 'px',
        'flex': '0 0 ' + (nBufferWidth + 2) + 'px'
      });
      $rightCounting.find('input[name="' + tInputName + '"]').remove();
      $rightCounting.append('<input type="hidden" name="' + tInputName + '" value="' + number_format($input.val(), 0, '.', ' ') + '">');

    });



  });
}

function getScrollbarWidth() {
  var outer = document.createElement("div");
  outer.style.visibility = "hidden";
  outer.style.width = "100px";
  outer.style.msOverflowStyle = "scrollbar";

  document.body.appendChild(outer);

  var widthNoScroll = outer.offsetWidth;
  // force scrollbars
  outer.style.overflow = "scroll";

  // add innerdiv
  var inner = document.createElement("div");
  inner.style.width = "100%";
  outer.appendChild(inner);

  var widthWithScroll = inner.offsetWidth;

  // remove divs
  outer.parentNode.removeChild(outer);
  return widthNoScroll - widthWithScroll;
}

function bodyScrollOff() {
  $('body').css({
    'overflow': 'hidden',
    'margin-right': getScrollbarWidth() + 'px',
  });
}

function bodyScrollOn() {
  $('body').removeAttr('style');
}

